.location {
    width: 100%;
    background-image: linear-gradient(to bottom,
            #ffffff 25%,
            #FFF5F6 25%,
            #FFF5F6 75%,
        );
    &__list{
        margin-bottom: 32px;
        @media screen and (min-width: 768px) {
                display: flex;
                margin-left: -32px;
                margin-bottom: 40px;
            }
        @media screen and (min-width: 1280px) {
                margin-bottom: 60px;
                }
    }
    &__item{
        &:not(:last-child) {
                margin-bottom: 20px;
            }
        @media screen and (min-width: 768px) {
        width: calc(100% / 3 - 32px);
        margin-left: 32px;
        margin-bottom: 0;
        }
    }

    &__content {
        background-color: #fff;
        box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
        border-radius: 24px;
        padding: 40px 32px 40px 32px;
        
    }

    &__category {
        color: #fff;
        font-weight: 500;
        font-size: 12px;
        line-height: 1.86;
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        border-radius: 4px;
        height: 16px;
        margin-bottom: 20px;
        display: table;
        vertical-align: middle;
        @media screen and (min-width: 768px){
            font-size: 16px;
        }
    }

    &__category--cafe {
        background-color: #F0D1A5;
        width: 80px;

    }

    &__category--foodtruck {
        background-color: #C2E297;
        width: 130px;
    }


    &__city {
        font-weight: 500;
        font-size: 12px;
        line-height: 1.86;
        letter-spacing: 0.04em;
        color: #000;
        margin-bottom: 8px;
        @media screen and (min-width: 768px) {
            font-size: 14px;
            }
        @media screen and (min-width: 1280px) {
            font-size: 16px;
            }
    }

    &__adress {
        color: var(--paragraf-grey);
        font-weight: 500;
        font-size: 12px;
        line-height: 1.5;
        margin-bottom: 48px;
        position: relative;
        @media screen and (min-width: 768px) {
                font-size: 14px;
                line-height: 1.66;
                margin-bottom: 64px;
            }
        
            @media screen and (min-width: 1280px) {
                font-size: 16px;
                line-height: 1.83;
            }

        &::after {
            content: '';
            display: block;
            background-color: #E1E1E1;
            height: 1px;
            width: 100%;
            position: absolute;
            left: 0;
            bottom: -24px;
            @media screen and (min-width: 768px) {
                    bottom: -32px;
                }
        }
    }

    &__workingdays {
        color: #000;
        font-weight: 500;
        font-size: 12px;
        line-height: 1.86;
        letter-spacing: 0.04em;
        @media screen and (min-width: 768px) {
                font-size: 14px;
            }
        
            @media screen and (min-width: 1280px) {
                font-size: 16px;
            }
    }

    &__workingtime {
        color: var(--ico-bg);
        font-weight: 500;
        font-size: 12px;
        line-height: 1.86;
        letter-spacing: 0.04em;
        margin-bottom: 8px;
        @media screen and (min-width: 768px) {
                font-size: 14px;
            }
        
            @media screen and (min-width: 1280px) {
                font-size: 16px;
            }
    }

    &__phone {
        display: block;
        color: var(--paragraf-grey);
        font-weight: 500;
        font-size: 12px;
        line-height: 1.5;
        text-decoration: none;
        margin-top: 48px;
        position: relative;
        @media screen and (min-width: 768px) {
                font-size: 14px;
                line-height: 1.83;
                margin-top: 64px;
            }
        
            @media screen and (min-width: 1280px) {
                font-size: 16px;
            }

        &::before {
            content: '';
            display: block;
            background-color: #E1E1E1;
            height: 1px;
            width: 100%;
            position: absolute;
            left: 0;
            top: -24px;
            @media screen and (min-width: 768px) {
                    top: -32px;
                }
        }
    }

    &__mail {
        color: var(--paragraf-grey);
        font-weight: 500;
        font-size: 12px;
        line-height: 1.5;
        text-decoration: none;
        @media screen and (min-width: 768px) {
                font-size: 14px;
                line-height: 1.83;
            }
        
            @media screen and (min-width: 1280px) {
                font-size: 16px;
            }
    }
}
.button-block{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    @media screen and (min-width:768px) {
        flex-direction: row;
        justify-content: center;
    }
}
.location__btn {
        display: block;
        width: 161px;
        height: 44px;
        border-radius: 22px;
        font-weight: 700;
        text-align: center;
        border: none;
        @media screen and (min-width:768px) {
            width: 191px;
        }
        @media screen and (min-width:1280px) {
            width: 200px;
        }
        

    }

.location__btn--dark {
        color: #fff;
        background-color: #D41342;
        margin-bottom: 10px;
        &:hover, &:focus {
            background-color: #C7133E;
            }
        @media screen and (min-width:768px) {
            margin-bottom: 0;
            margin-right: 15px;
        }
    }

.location__btn--light {
        color: var(--text-accent);
        background-color: #fff;
        &:hover, &:focus {
            background-color: #ECEBEB;
        } 
    }

.vector {
    fill: #ffffff;
}
.vector-light{
    fill: var(--text-accent)
}