h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

body {
  color: var(--text-main);
  background-color: var(--section-white);
  letter-spacing: .04em;
  font-family: DM sans, sans-serif;
  font-size: 16px;
  line-height: 1.76;
}

.list {
  padding-left: 0;
  list-style-type: none;
}

:root {
  --text-main: #fff;
  --text-accent: #d41443;
  --hero-bg: #ffb8ca;
  --section-white: #e5e5e5;
  --section-rose: #fff5f6;
  --paragraf-black: #000;
  --paragraf-grey: #907e82;
  --border-in: #e1e1e1;
  --ico-bg: #fea5bb;
  --href-top: #ffa5ba;
  --bg-btn: #d41342;
  --bg-btn-hover-red: #c7133e;
  --bg-btn-hover-white: #ecebeb;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  outline: 2px solid tomato;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  justify-content: center;
  align-items: center;
  transition: opacity .25s, visibility .25s;
  display: flex;
  position: fixed;
  top: 0;
}

.section-header {
  text-transform: uppercase;
  color: var(--href-top);
  font-family: Titan One;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.16;
}

@media screen and (min-width: 768px) {
  .section-header {
    font-size: 26px;
    line-height: 1.15;
  }
}

@media screen and (min-width: 1280px) {
  .section-header {
    width: auto;
    letter-spacing: .04em;
    font-size: 30px;
    line-height: 1.13;
  }
}

.section-header__bold {
  text-transform: uppercase;
  color: var(--text-accent);
  font-family: Titan One;
  font-size: 34px;
  font-weight: 400;
  line-height: 1.08;
}

@media screen and (min-width: 768px) {
  .section-header__bold {
    font-size: 48px;
    line-height: 1.14;
  }
}

@media screen and (min-width: 1280px) {
  .section-header__bold {
    letter-spacing: .06px;
    font-size: 58px;
    line-height: 1.13;
  }
}

.header {
  background-color: var(--hero-bg);
  position: relative;
}

.site-nav_short-menu {
  align-items: center;
  display: flex;
}

@media screen and (min-width: 1280px) {
  .site-nav_short-menu {
    justify-content: space-between;
  }
}

.link {
  text-decoration: none;
}

@media screen and (max-width: 1279px) {
  .site-nav_logo-link {
    margin-right: auto;
  }
}

.site-nav_btn-burger {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: auto;
}

@media screen and (min-width: 1280px) {
  .site-nav_btn-burger {
    display: none;
  }
}

.site-nav_icon-open {
  fill: var(--text-accent);
  transition: fill .25s linear;
}

.site-nav_btn-burger:hover .site-nav_icon-open {
  fill: var(--text-main);
}

.site-nav_btn-cross {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 20px;
  right: 12px;
}

.site-nav_icon-close {
  fill: var(--text-main);
  transition: fill .25s linear;
}

.site-nav_btn-cross:hover .site-nav_icon-close {
  fill: var(--text-accent);
}

.mob-menu {
  height: 100vh;
  z-index: 999;
  width: 250px;
  background-color: #e17992;
  padding-top: 60px;
  padding-left: 20px;
  padding-right: 20px;
  transition: opacity .25s linear, visibility .25s linear;
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  overflow: scroll;
}

@media screen and (min-width: 768px) {
  .mob-menu {
    padding-left: 34px;
    padding-right: 34px;
  }
}

@media screen and (min-width: 1280px) {
  .mob-menu {
    display: none;
  }
}

.mob-menu_btn {
  min-width: 209px;
  height: 44px;
  background-color: var(--text-main);
  text-align: center;
  color: var(--text-accent);
  cursor: pointer;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 22px;
  margin-bottom: 32px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.31;
  transition: background-color .25s linear;
  display: block;
  position: relative;
}

.mob-menu_btn:hover {
  background-color: var(--bg-btn-hover-white);
}

@media screen and (min-width: 768px) {
  .mob-menu_btn {
    min-width: 186px;
  }
}

.mob-menu_btn-icon {
  fill: var(--text-accent);
  position: absolute;
  top: 40%;
  right: 20%;
}

.mob-menu_item {
  border-top: 1px solid #e18298;
  border-bottom: 1px solid #e18298;
  padding-top: 11px;
  padding-bottom: 13px;
  position: relative;
}

.mob-menu_link {
  color: var(--text-main);
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  transition: color .25s linear;
}

.current, .mob-menu_link:focus, .mob-menu_link:hover {
  color: var(--text-accent);
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.tab-menu_btn {
  min-width: 160px;
  height: 44px;
  background-color: var(--text-main);
  text-align: center;
  color: var(--text-accent);
  cursor: pointer;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 22px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.31;
  transition: background-color .25s linear;
  display: block;
  position: relative;
}

@media screen and (max-width: 767px) {
  .tab-menu_btn {
    display: none;
  }
}

@media screen and (max-width: 1279px) {
  .tab-menu_btn {
    margin-left: 32px;
  }
}

.tab-menu_btn:hover, .tab-menu_btn:focus {
  background-color: var(--bg-btn-hover-white);
}

.tab-menu_btn-icon {
  fill: var(--text-accent);
  position: absolute;
  top: 40%;
  right: 15%;
}

@media screen and (max-width: 1279px) {
  .desktop-menu {
    display: none;
  }
}

.desktop-menu_list {
  display: flex;
}

.desktop-menu_item:not(:last-child) {
  margin-right: 35px;
}

.desktop-menu_link {
  color: var(--text-main);
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  transition: color .25s linear;
}

.desktop-menu_link:hover, .desktop-menu_link:focus, .desktop-menu_link.current {
  color: var(--text-accent);
}

.overlay-header {
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  background-color: rgba(94, 0, 0, .5);
  transition: opacity .25s linear, visibility .25s linear;
  position: fixed;
  top: 0;
}

.modal-header {
  width: 320px;
  max-width: 100%;
  height: 650px;
  max-height: 100%;
  z-index: 200;
  background-color: #e17992;
  border-radius: 24px;
  padding: 45px 17px;
  transition: transform .25s linear;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .modal-header {
    width: 450px;
  }
}

@media screen and (min-width: 1280px) {
  .modal-header {
    width: 500px;
    height: 700px;
  }
}

.modal-header_btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  transition: fill .25s linear;
  position: absolute;
  top: 18px;
  right: 18px;
}

@media screen and (min-width: 768px) {
  .modal-header_btn {
    top: 28px;
    right: 28px;
  }
}

.modal-header_icon {
  fill: var(--text-main);
}

.modal-header_btn:hover .modal-header_icon, .modal-header_btn:focus .modal-header_icon {
  fill: var(--text-accent);
}

.modal-header_title {
  text-align: center;
  letter-spacing: .04em;
  color: var(--text-main);
  margin-bottom: 24px;
  font-family: Titan One;
  font-size: 38px;
  font-weight: 400;
  line-height: 1.16;
}

.modal-header_field {
  margin-bottom: 14px;
}

.modal-header_lable {
  color: #c7133e;
  margin-bottom: 9px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  display: block;
}

.modal-header_input {
  width: 100%;
  height: 40px;
  color: rgba(199, 19, 62, .7);
  background-color: #ffb8ca;
  border: none;
  border-radius: 22px;
  outline: none;
  padding-left: 12px;
}

.modal-header_input::-ms-input-placeholder {
  color: rgba(199, 19, 62, .7);
  font-family: DM Sans;
  font-size: 14px;
  line-height: 1.29;
}

.modal-header_input::placeholder {
  color: rgba(199, 19, 62, .7);
  font-family: DM Sans;
  font-size: 14px;
  line-height: 1.29;
}

.modal-header_comment-wrap {
  margin-bottom: 20px;
}

.modal-header_comment-form {
  width: 100%;
  height: 110px;
  resize: none;
  color: rgba(199, 19, 62, .7);
  background-color: #ffb8ca;
  border: none;
  border-radius: 22px;
  outline: none;
  padding: 12px;
}

.modal-header_comment-form::-ms-input-placeholder {
  color: rgba(199, 19, 62, .7);
  font-family: DM Sans;
  font-size: 14px;
  line-height: 1.29;
}

.modal-header_comment-form::placeholder {
  color: rgba(199, 19, 62, .7);
  font-family: DM Sans;
  font-size: 14px;
  line-height: 1.29;
}

.modal-header_check-thumb {
  justify-content: center;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
}

.modal-header_check-label {
  color: var(--text-main);
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
}

.modal-header_check-label:before {
  content: "";
  width: 12px;
  height: 12px;
  border: 1px solid #fff;
  border-radius: 3px;
  margin-right: 8px;
  transition: background-color .25s linear, background-image .25s linear;
  display: inline-block;
}

.checkbox-modal:checked + .modal-header_check-label:before {
  background-color: rgba(199, 19, 62, .5);
  background-image: url("advantages3.08bd8f0d.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
}

.modal-header_form-button {
  width: 140px;
  height: 40px;
  color: var(--text-main);
  background-color: #c7133e;
  border: none;
  border-radius: 20px;
  margin: 0 auto;
  display: block;
}

.modal-header_form-button:hover, .modal-header_form-button:focus {
  background-color: #741028;
}

